let tab = [{
      path: 'intention',
      tabName: '租售意向单',
      tabClass: 'zhu',
      lists: ['租赁意向单', '购买意向单']
    }, {
      path: 'order',
      tabName: '我的订单',
      tabClass: 'order',
      lists: ['全部', '待付款', '待发货', '待收货']
    }, {
      path: 'aftersale',
      tabName: '退款售后',
      tabClass: 'shouhou2',
      lists: ['全部']
    }, {
      path: 'cart',
      tabName: '我的购物车',
      tabClass: 'gouwuche2',
      lists: ['叉车租赁', '叉车售卖','配件']
    }, {
      path: 'collect',
      tabName: '我的收藏夹',
      tabClass: 'save',
      lists: ['我收藏的配件']
    }, {
      path: 'setting',
      tabName: '个人设置',
      tabClass: 'shouhou',
      lists: ['编辑个人资料', '地址管理', '安全设置'],
      children: [
        { path: 'info' },
        { path: 'address' },
        { path: 'safe' }
      ]
    }, {
      path: 'feedback',
      tabName: '意见反馈',
      tabClass: 'post',
      lists: ['意见反馈']
    }, ]

export default tab



